
"use client";

import { useState } from 'react';
import type { Project } from '@/types/project';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import NextImage from "next/image";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog"; // Removed AlertDialogTrigger from here as it's not used directly for the shared dialog
import { Eye, Bot, ExternalLink, Code2 } from "lucide-react";
import { useTranslations } from '@/hooks/useTranslations';

interface ProjectsSectionProps {
  projects: Project[];
  onViewImage: (project: Project) => void;
  onAskAI: (project: Project) => void;
  isLoading?: boolean;
  error?: string | null;
}

export default function ProjectsSection({ projects, onViewImage, onAskAI, isLoading, error }: ProjectsSectionProps) {
  const t = useTranslations();
  const [currentProjectForCode, setCurrentProjectForCode] = useState<Project | null>(null);

  if (isLoading) {
    return (
      <section id="projects" className="py-16">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-primary">{t.projectsSection.title}</h2>
          <p className="text-md text-muted-foreground mt-2">{t.projectsSection.loading}</p>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {[...Array(3)].map((_, index) => (
            <Card key={index} className="shadow-xl bg-card/80 backdrop-blur-sm flex flex-col animate-pulse">
              <CardHeader className="p-4">
                <div className="relative aspect-[16/10] w-full mb-3 bg-muted rounded-t-md"></div>
                <div className="h-6 bg-muted rounded w-3/4"></div>
              </CardHeader>
              <CardContent className="p-4 pt-0 flex-grow">
                <div className="h-4 bg-muted rounded w-full mb-2"></div>
                <div className="h-4 bg-muted rounded w-full mb-2"></div>
                <div className="h-4 bg-muted rounded w-5/6"></div>
              </CardContent>
              <CardFooter className="p-4 pt-2 flex flex-col sm:flex-row gap-2 items-stretch">
                <div className="h-10 bg-muted rounded w-full"></div>
                <div className="h-10 bg-muted rounded w-full"></div>
              </CardFooter>
            </Card>
          ))}
        </div>
      </section>
    );
  }

  if (error) {
    return (
      <section id="projects" className="py-16">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-primary">{t.projectsSection.title}</h2>
          <p className="text-md text-destructive mt-2">{error}</p>
        </div>
      </section>
    );
  }

  if (!projects || projects.length === 0) {
     return (
      <section id="projects" className="py-16">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-primary">{t.projectsSection.title}</h2>
          <p className="text-md text-muted-foreground mt-2">{t.projectsSection.noProjects}</p>
        </div>
      </section>
    );
  }

  return (
    <section id="projects" className="py-16">
      <div className="text-center mb-12">
        <h2 className="text-3xl md:text-4xl font-bold text-primary">{t.projectsSection.title}</h2>
        <p className="text-md text-muted-foreground mt-2">{t.projectsSection.subtitle}</p>
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        {projects.map((project) => (
          <Card key={project.id} className="shadow-xl bg-card/80 backdrop-blur-sm tilt-card flex flex-col">
            <CardHeader className="p-4">
              <div className="relative aspect-[16/10] w-full mb-3 group">
                <NextImage
                  src={project.imageUrl || 'https://placehold.co/600x400.png'}
                  alt={project.title}
                  layout="fill"
                  objectFit="cover"
                  className="rounded-t-md"
                  data-ai-hint={project.dataAiHint}
                />
                 <Button
                  variant="ghost"
                  size="icon"
                  className="absolute top-2 right-2 bg-black/40 hover:bg-black/60 text-white hover:text-primary rounded-full h-9 w-9 z-10"
                  onClick={() => onViewImage(project)}
                  aria-label={t.projectsSection.viewImageAriaLabel.replace('{projectTitle}', project.title)}
                  title={t.projectsSection.viewImageAriaLabel.replace('{projectTitle}', project.title)}
                >
                  <Eye className="h-5 w-5" />
                </Button>
              </div>
              <CardTitle className="text-xl text-primary group-hover:text-primary/90 transition-colors">{project.title}</CardTitle>
            </CardHeader>
            <CardContent className="p-4 pt-0 flex-grow">
              <CardDescription className="text-sm text-card-foreground/80 line-clamp-3 mb-3">
                {project.description}
              </CardDescription>
              {project.technologies && project.technologies.length > 0 && (
                <div className="flex flex-wrap gap-1.5 mb-3">
                  {project.technologies.map(tech => (
                    <Badge key={tech} variant="secondary" className="text-xs px-2 py-0.5 bg-secondary/20 border-secondary/30 text-secondary-foreground">
                      {tech}
                    </Badge>
                  ))}
                </div>
              )}
            </CardContent>
            <CardFooter className="p-4 pt-2 flex flex-wrap gap-2 items-stretch">
              {project.link && (
                <Button asChild variant="outline" className="flex-grow border-primary/50 hover:border-primary text-primary hover:bg-primary/10 text-xs sm:text-sm">
                  <a href={project.link} target="_blank" rel="noopener noreferrer">
                    <ExternalLink className="mr-1.5 h-3.5 w-3.5 sm:mr-2 sm:h-4 sm:w-4" /> {t.projectsSection.viewProjectButton}
                  </a>
                </Button>
              )}
              {project.repositoryUrl && (
                <Button 
                  variant="outline" 
                  className="flex-grow border-muted-foreground/30 hover:border-muted-foreground text-muted-foreground hover:bg-muted/20 text-xs sm:text-sm"
                  onClick={() => setCurrentProjectForCode(project)}
                  title={t.projectsSection.viewCodeTooltip}
                  aria-label={t.projectsSection.viewCodeAriaLabel.replace('{projectTitle}', project.title)}
                >
                  <Code2 className="mr-1.5 h-3.5 w-3.5 sm:mr-2 sm:h-4 sm:w-4" /> {t.projectsSection.viewCodeButton}
                </Button>
              )}
              <Button
                variant="outline"
                onClick={() => onAskAI(project)}
                className="flex-grow border-secondary/50 hover:border-secondary text-secondary hover:bg-secondary/10 text-xs sm:text-sm"
                aria-label={t.projectsSection.askAgentAriaLabel.replace('{projectTitle}', project.title)}
                title={t.projectsSection.askAgentTooltip}
              >
                <Bot className="mr-1.5 h-3.5 w-3.5 sm:mr-2 sm:h-4 sm:w-4" /> {t.projectsSection.askAgentButton}
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>
      {currentProjectForCode && currentProjectForCode.repositoryUrl && (
        <AlertDialog open={!!currentProjectForCode} onOpenChange={(isOpen) => { if (!isOpen) setCurrentProjectForCode(null); }}>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>{t.projectsSection.codeLinkModalTitle}</AlertDialogTitle>
              <AlertDialogDescription>
                {t.projectsSection.codeLinkModalDescription}
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogCancel onClick={() => setCurrentProjectForCode(null)}>{t.threeCanvas.closeButton || "Cancel"}</AlertDialogCancel>
              <AlertDialogAction asChild>
                <a href={currentProjectForCode.repositoryUrl} target="_blank" rel="noopener noreferrer">
                  {t.projectsSection.codeLinkModalButton}
                </a>
              </AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      )}
    </section>
  );
}

