
"use client";

import { useEffect, useRef } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { timelineEventsData } from "@/data/timelineEvents";
import type { LucideIconComponent } from "@/types/timeline";
import { useTranslations } from "@/hooks/useTranslations";
import * as LucideIcons from 'lucide-react';
import { cn } from "@/lib/utils";

const getIcon = (iconName?: keyof typeof LucideIcons): LucideIconComponent | null => {
  if (!iconName) return null;
  const IconComponent = LucideIcons[iconName] as LucideIconComponent | undefined;
  return IconComponent || LucideIcons.CalendarDays; // Default icon
};

export default function TimelineSection() {
  const t = useTranslations();
  const timelineTranslations = t.timeline || {};
  const eventsTranslations = timelineTranslations.events || {};
  const itemRefs = useRef<(HTMLDivElement | null)[]>([]);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add('visible');
            observer.unobserve(entry.target);
          }
        });
      },
      { threshold: 0.2, rootMargin: "0px 0px -50px 0px" } // Trigger when 20% visible, slightly offset
    );

    itemRefs.current.forEach((ref) => {
      if (ref) observer.observe(ref);
    });

    return () => {
      itemRefs.current.forEach((ref) => {
        if (ref) observer.unobserve(ref);
      });
    };
  }, [timelineEventsData]);

  return (
    <section id="timeline" className="py-16">
      <div className="text-center mb-12 md:mb-16">
        <h2 className="text-3xl md:text-4xl font-bold text-primary">
          {timelineTranslations.title || "My Journey"}
        </h2>
        <p className="text-md text-muted-foreground mt-2 px-2">
          {timelineTranslations.subtitle || "A brief history of my milestones and experiences."}
        </p>
      </div>

      <div className="relative max-w-3xl mx-auto px-4 sm:px-0">
        {/* Central line for the timeline - made thinner */}
        <div className="absolute left-4 md:left-1/2 top-0 bottom-0 w-px bg-border/90 transform md:-translate-x-1/2" aria-hidden="true"></div>

        {timelineEventsData.map((event, index) => {
          const IconComponent = getIcon(event.iconName);
          const eventLocaleData = eventsTranslations[event.id as keyof typeof eventsTranslations] || {};
          const dateText = eventLocaleData.date || event.dateKey;
          const titleText = eventLocaleData.title || event.titleKey;
          const descriptionText = eventLocaleData.description || event.descriptionKey;
          
          // For alternating layout on desktop
          const isEven = index % 2 === 0;

          return (
            <div 
              key={event.id}
              ref={el => itemRefs.current[index] = el} 
              className={cn(
                "mb-10 md:mb-12 flex w-full items-start timeline-card-animate",
                isEven ? "md:flex-row-reverse timeline-card-right" : "md:flex-row timeline-card-left", // Alternating classes for animation
                "md:items-center" // Align items center on medium+ screens
              )}
            >
              {/* Spacer for alternating layout on md+ screens */}
              <div className="hidden md:block md:w-1/2"></div>

              {/* Wrapper for icon on the line and the card itself */}
              <div className={cn(
                "relative w-full md:w-1/2",
                // Increased padding for more space between line and card
                isEven ? "md:pl-12" : "md:pr-12", 
                "pl-12" // Increased left padding for mobile
              )}>
                {/* Icon positioned on the central line */}
                <div className={cn(
                    "absolute top-1 transform -translate-y-1/2 z-10",
                    // Mobile: icon dot on the left, aligned with the central line (left-4 is line, icon width is w-8, so -1rem from CardWrapper start)
                    "left-[calc(1rem-theme(spacing.4))]", // (1rem is half icon width (w-8 = 2rem / 2 = 1rem), theme(spacing.4) is line offset from edge)
                    // Desktop: icon dot positioned on the central line
                    isEven 
                      ? "md:right-full md:-mr-[1rem]" // Icon to the left of card, half its width pulls it onto the line
                      : "md:left-full md:-ml-[1rem]"  // Icon to the right of card, half its width pulls it onto the line
                  )}
                >
                  <div className={cn(
                      "w-8 h-8 rounded-full flex items-center justify-center text-primary-foreground shadow-md",
                      event.iconBgColor || 'bg-primary'
                    )}
                  >
                    {IconComponent && <IconComponent className="w-5 h-5" />}
                  </div>
                </div>
                
                <Card className="shadow-lg hover:shadow-xl transition-shadow duration-300 bg-card/80 backdrop-blur-sm w-full tilt-card">
                  <CardHeader className="pb-2 pt-4 px-4">
                    <p className="text-xs font-semibold text-muted-foreground">{dateText}</p>
                    <CardTitle className="text-lg md:text-xl text-primary">{titleText}</CardTitle>
                  </CardHeader>
                  <CardContent className="pb-4 px-4">
                    <CardDescription className="text-sm text-card-foreground/80 mb-3">
                      {descriptionText}
                    </CardDescription>
                    {event.tags && event.tags.length > 0 && (
                      <div className="flex flex-wrap gap-1.5">
                        {event.tags.map((tag) => (
                          <Badge 
                            key={tag} 
                            variant="secondary" 
                            className="text-xs px-2 py-0.5 bg-secondary/20 border border-secondary/50 text-secondary-foreground hover:bg-secondary/30 hover:border-secondary/50 transition-colors cursor-default"
                          >
                            {tag} 
                          </Badge>
                        ))}
                      </div>
                    )}
                  </CardContent>
                </Card>
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
}
