
"use client";

import type { Project } from '@/types/project';
import React, { useRef, useEffect, useState, useMemo } from 'react';
import * as THREE from 'three';
import { useThemeContext } from '@/contexts/ThemeContext';

// Simple Meteor class
class Meteor {
  mesh: THREE.Mesh;
  velocity: THREE.Vector3;
  sceneSize: number;
  initialPosition: THREE.Vector3;

  constructor(sceneSize: number) {
    this.sceneSize = sceneSize;
    const geometry = new THREE.SphereGeometry(Math.random() * 0.07 + 0.02, 6, 6);
    const material = new THREE.MeshBasicMaterial({ color: 0xffffee, transparent: true, opacity: 0.5 + Math.random() * 0.2 });
    this.mesh = new THREE.Mesh(geometry, material);
    this.initialPosition = new THREE.Vector3();
    this.reset();
  }

  reset() {
    const spread = this.sceneSize * 1.3;
    const edge = Math.floor(Math.random() * 4);

    let x, y, z;
    // Start meteors further out for a better cross-screen effect
    const startDistanceFactor = 1.5;

    // Determine starting position (off-screen) and a target on the opposite side
    if (edge === 0) { // Start from left
      x = -spread * startDistanceFactor;
      y = THREE.MathUtils.randFloatSpread(spread);
      z = THREE.MathUtils.randFloatSpread(this.sceneSize * 0.5);
      this.velocity = new THREE.Vector3(THREE.MathUtils.randFloat(0.05, 0.15), THREE.MathUtils.randFloatSpread(0.02), THREE.MathUtils.randFloatSpread(0.01)).normalize().multiplyScalar(THREE.MathUtils.randFloat(0.06, 0.12));
    } else if (edge === 1) { // Start from right
      x = spread * startDistanceFactor;
      y = THREE.MathUtils.randFloatSpread(spread);
      z = THREE.MathUtils.randFloatSpread(this.sceneSize * 0.5);
      this.velocity = new THREE.Vector3(-THREE.MathUtils.randFloat(0.05, 0.15), THREE.MathUtils.randFloatSpread(0.02), THREE.MathUtils.randFloatSpread(0.01)).normalize().multiplyScalar(THREE.MathUtils.randFloat(0.06, 0.12));
    } else if (edge === 2) { // Start from top
      x = THREE.MathUtils.randFloatSpread(spread);
      y = spread * startDistanceFactor;
      z = THREE.MathUtils.randFloatSpread(this.sceneSize * 0.5);
      this.velocity = new THREE.Vector3(THREE.MathUtils.randFloatSpread(0.02), -THREE.MathUtils.randFloat(0.05, 0.15), THREE.MathUtils.randFloatSpread(0.01)).normalize().multiplyScalar(THREE.MathUtils.randFloat(0.06, 0.12));
    } else { // Start from bottom
      x = THREE.MathUtils.randFloatSpread(spread);
      y = -spread * startDistanceFactor;
      z = THREE.MathUtils.randFloatSpread(this.sceneSize * 0.5);
      this.velocity = new THREE.Vector3(THREE.MathUtils.randFloatSpread(0.02), THREE.MathUtils.randFloat(0.05, 0.15), THREE.MathUtils.randFloatSpread(0.01)).normalize().multiplyScalar(THREE.MathUtils.randFloat(0.06, 0.12));
    }
    this.initialPosition.set(x, y, z);
    this.mesh.position.copy(this.initialPosition);
  }

  update() {
    this.mesh.position.add(this.velocity);
    // Reset if meteor is too far from the center (went across the screen)
    if (this.mesh.position.length() > this.sceneSize * 2) {
      this.reset();
    }
  }
}


interface ThreeCanvasProps {
  projects: Project[];
}

const ThreeCanvas: React.FC<ThreeCanvasProps> = ({ projects }) => {
  const mountRef = useRef<HTMLDivElement>(null);
  const animationFrameIdRef = useRef<number | null>(null);
  const { theme } = useThemeContext();
  const [isLoading, setIsLoading] = useState(true);

  const projectMeshesRef = useRef<THREE.Mesh[]>([]);
  const meteorsRef = useRef<Meteor[]>([]);
  const sceneRef = useRef<THREE.Scene | null>(null);
  const cameraRef = useRef<THREE.PerspectiveCamera | null>(null);
  const rendererRef = useRef<THREE.WebGLRenderer | null>(null);
  const backgroundShaderMaterialRef = useRef<THREE.ShaderMaterial | null>(null);
  const backgroundGeometryRef = useRef<THREE.SphereGeometry | null>(null);
  const clockRef = useRef(new THREE.Clock());


  const themeColors = useMemo(() => {
    if (typeof document === 'undefined') {
        return {
            uColor1Dark: new THREE.Color(0x02040a),
            uColor2Dark: new THREE.Color(0x1a2a78),
            uColor3Dark: new THREE.Color(0x4a0072),
            uColor1Light: new THREE.Color(0xe0f2fe),
            uColor2Light: new THREE.Color(0x7dd3fc),
            uColor3Light: new THREE.Color(0x8b5cf6)
        };
    }
    const computedRootStyle = getComputedStyle(document.documentElement);
    const getHSLColor = (cssVar: string, fallbackHex: string) => {
      const hslString = computedRootStyle.getPropertyValue(cssVar).trim();
      if (hslString) {
        const [h, s, l] = hslString.split(' ').map(val => parseFloat(val.replace('%', '')));
        if (!isNaN(h) && !isNaN(s) && !isNaN(l)) {
          const color = new THREE.Color();
          color.setHSL(h / 360, s / 100, l / 100);
          return color;
        }
      }
      return new THREE.Color(fallbackHex);
    };

    return {
      uColor1Dark: getHSLColor('--background', '#02040a'),
      uColor2Dark: getHSLColor('--primary', '#1a2a78'),
      uColor3Dark: getHSLColor('--accent', '#4a0072'),
      uColor1Light: getHSLColor('--background', '#e0f2fe'),
      uColor2Light: getHSLColor('--primary', '#7dd3fc'),
      uColor3Light: getHSLColor('--accent', '#8b5cf6')
    };
  }, []);


  useEffect(() => {
    if (!mountRef.current || typeof window === 'undefined') return;

    setIsLoading(true);
    const currentMount = mountRef.current;

    let canvasWidth = currentMount.clientWidth;
    let canvasHeight = currentMount.clientHeight;

    if (canvasWidth === 0 || canvasHeight === 0) {
      console.warn("ThreeCanvas: Mount point has zero dimensions. Retrying or skipping setup.");
      const retryTimeout = setTimeout(() => {
         if (!mountRef.current) return;
         canvasWidth = mountRef.current.clientWidth;
         canvasHeight = mountRef.current.clientHeight;
         if (canvasWidth === 0 || canvasHeight === 0) {
            console.error("ThreeCanvas: Mount point still has zero dimensions. Skipping Three.js setup.");
            setIsLoading(false);
            return;
         }
      }, 100);
      return () => clearTimeout(retryTimeout);
    }


    sceneRef.current = new THREE.Scene();
    cameraRef.current = new THREE.PerspectiveCamera(75, canvasWidth / canvasHeight, 0.1, 1000);
    cameraRef.current.position.set(0, 0, 15);

    rendererRef.current = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    rendererRef.current.setSize(canvasWidth, canvasHeight);
    rendererRef.current.setPixelRatio(window.devicePixelRatio);

    while (currentMount.firstChild) {
        currentMount.removeChild(currentMount.firstChild);
    }
    currentMount.appendChild(rendererRef.current.domElement);

    const ambientLight = new THREE.AmbientLight(0xffffff, 0.6);
    sceneRef.current.add(ambientLight);
    const directionalLight = new THREE.DirectionalLight(0xffffff, 1.0);
    directionalLight.position.set(5, 10, 7.5);
    sceneRef.current.add(directionalLight);

    backgroundGeometryRef.current = new THREE.SphereGeometry(500, 60, 40);
    backgroundGeometryRef.current.scale(-1, 1, 1);

    backgroundShaderMaterialRef.current = new THREE.ShaderMaterial({
        uniforms: {
            uTime: { value: 0 },
            uTheme: { value: theme === 'dark' ? 0.0 : 1.0 },
            uColor1Dark: { value: themeColors.uColor1Dark },
            uColor2Dark: { value: themeColors.uColor2Dark },
            uColor3Dark: { value: themeColors.uColor3Dark },
            uColor1Light: { value: themeColors.uColor1Light },
            uColor2Light: { value: themeColors.uColor2Light },
            uColor3Light: { value: themeColors.uColor3Light },
        },
      vertexShader: `
        varying vec2 vUv;
        void main() {
          vUv = uv;
          gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
        }
      `,
      // Shader for static stars and animated nebula
      fragmentShader: `
        uniform float uTime;
        uniform float uTheme;
        uniform vec3 uColor1Dark;
        uniform vec3 uColor2Dark;
        uniform vec3 uColor3Dark;
        uniform vec3 uColor1Light;
        uniform vec3 uColor2Light;
        uniform vec3 uColor3Light;
        varying vec2 vUv;

        float random(vec2 st) {
            return fract(sin(dot(st.xy, vec2(12.9898,78.233))) * 43758.5453123);
        }

        float noise(vec2 st) {
            vec2 i = floor(st);
            vec2 f = fract(st);
            float a = random(i);
            float b = random(i + vec2(1.0, 0.0));
            float c = random(i + vec2(0.0, 1.0));
            float d = random(i + vec2(1.0, 1.0));
            vec2 u = f * f * (3.0 - 2.0 * f);
            return mix(a, b, u.x) + (c - a) * u.y * (1.0 - u.x) + (d - b) * u.y * u.x;
        }

        float fbm(vec2 st) {
            float value = 0.0;
            float amplitude = 0.5;
            for (int i = 0; i < 6; i++) {
                value += amplitude * noise(st);
                st *= 2.0;
                amplitude *= 0.5;
            }
            return value;
        }

        void main() {
            vec2 uv = vUv;
            float nebulaTime = uTime * 0.03; // Time for nebula animation ONLY

            vec3 color1 = mix(uColor1Dark, uColor1Light, uTheme);
            vec3 color2 = mix(uColor2Dark, uColor2Light, uTheme);
            vec3 color3 = mix(uColor3Dark, uColor3Light, uTheme);

            vec3 baseColor = color1 * (uTheme > 0.5 ? 0.8 : 0.25);

            // Nebula / Galaxy Clouds (Animated)
            float nebulaPattern = fbm(uv * 2.8 + nebulaTime * 0.05 + vec2(sin(nebulaTime*0.035), cos(nebulaTime*0.055)));
            vec3 nebulaColoration = mix(color2, color3, sin(uv.x * 3.5 + nebulaTime * 0.12 + nebulaPattern * 2.2));
            float nebulaStrength = smoothstep(0.3, 0.7, nebulaPattern) * (uTheme > 0.5 ? 0.35 : 0.75);

            // Static Stars (Not affected by 'nebulaTime' for position)
            float starDensity = uTheme > 0.5 ? 550.0 : 900.0; // Increased star density
            float starSeed = random(floor(uv * starDensity));
            float starBrightness = pow(starSeed, uTheme > 0.5 ? 50.0 : 70.0); // Sharper, brighter stars
            vec3 starColorEffect = vec3(starBrightness * (uTheme > 0.5 ? 1.0 : 0.85)); // Slightly adjust brightness based on theme

            vec3 finalColor = baseColor + nebulaColoration * nebulaStrength + starColorEffect;

            // Vignette
            float dist = distance(uv, vec2(0.5));
            finalColor *= smoothstep(0.9, 0.35, dist);

            gl_FragColor = vec4(clamp(finalColor, 0.0, 1.0), 1.0);
        }
      `,
      side: THREE.BackSide,
    });
    const backgroundMesh = new THREE.Mesh(backgroundGeometryRef.current, backgroundShaderMaterialRef.current);
    sceneRef.current.add(backgroundMesh);

    // Clear previous project meshes if any
    projectMeshesRef.current.forEach(mesh => sceneRef.current?.remove(mesh));
    projectMeshesRef.current = [];

    const numSpiralElements = projects.length > 0 ? Math.min(projects.length, 30) : 20; // More elements for a denser spiral
    const a = 0.3; // Spiral parameter: initial radius factor (tighter start)
    const b = 0.25; // Spiral parameter: tightness/expansion rate (tighter spiral)
    const numTurns = 7; // Number of turns in the spiral (more turns for a prominent 3D effect)
    const verticalSpreadFactor = 7; // Increased vertical spread for more 3D depth

    for (let i = 0; i < numSpiralElements; i++) {
      const geometry = new THREE.IcosahedronGeometry(Math.random() * 0.25 + 0.1, 0); // Slightly smaller for denser look
      const projectColor = new THREE.Color(theme === 'dark' ? themeColors.uColor2Dark : themeColors.uColor2Light);
      projectColor.lerp(new THREE.Color(theme === 'dark' ? themeColors.uColor3Dark : themeColors.uColor3Light), Math.random() * 0.5 + 0.3);

      const material = new THREE.MeshStandardMaterial({
        color: projectColor,
        roughness: 0.4,
        metalness: 0.6,
        transparent: true,
        opacity: 0.7 + Math.random() * 0.2 // Slightly more opaque
      });
      const mesh = new THREE.Mesh(geometry, material);

      const t = (i / (numSpiralElements > 1 ? numSpiralElements - 1 : 1));
      const angle = t * numTurns * Math.PI * 2;
      const radius = a * Math.exp(b * angle);

      mesh.position.set(
        Math.cos(angle) * radius,
        (t - 0.5) * verticalSpreadFactor + Math.sin(angle * 0.7 + i * 0.2) * (radius * 0.15), // Y with more pronounced 3D wave
        Math.sin(angle) * radius - 2 // Z: on spiral, slightly closer to camera
      );
      mesh.rotation.set(Math.random() * Math.PI, Math.random() * Math.PI, Math.random() * Math.PI);
      projectMeshesRef.current.push(mesh);
      sceneRef.current.add(mesh);
    }

    // Clear previous meteors if any
    meteorsRef.current.forEach(meteor => sceneRef.current?.remove(meteor.mesh));
    meteorsRef.current = [];
    const numMeteors = 50; // More meteors
    const meteorSceneSize = 40;
    for (let i = 0; i < numMeteors; i++) {
      const meteor = new Meteor(meteorSceneSize);
      meteorsRef.current.push(meteor);
      sceneRef.current.add(meteor.mesh);
    }

    setIsLoading(false);


    const animate = () => {
      animationFrameIdRef.current = requestAnimationFrame(animate);
      const elapsedTime = clockRef.current.getElapsedTime();

      const scene = sceneRef.current;
      const camera = cameraRef.current;
      const renderer = rendererRef.current;
      const backgroundShaderMaterial = backgroundShaderMaterialRef.current;

      if (!scene || !camera || !renderer || !backgroundShaderMaterial) return;


      if (backgroundShaderMaterial?.uniforms?.uTime) {
        backgroundShaderMaterial.uniforms.uTime.value = elapsedTime;
      }
      if (backgroundShaderMaterial?.uniforms?.uTheme) {
         backgroundShaderMaterial.uniforms.uTheme.value = theme === 'dark' ? 0.0 : 1.0;
      }


      projectMeshesRef.current.forEach((mesh, idx) => {
        mesh.rotation.x += (0.0005 + Math.random()*0.0004) * (idx % 2 === 0 ? 1: -1);
        mesh.rotation.y += (0.0005 + Math.random()*0.0004) * (idx % 3 === 0 ? 1: -1);
      });

      meteorsRef.current.forEach(meteor => meteor.update());

      // Keep camera static for a fixed star background
      // camera.position.x = Math.sin(elapsedTime * 0.025) * 0.8; // Removed
      // camera.position.y = Math.cos(elapsedTime * 0.02) * 0.4;  // Removed
      camera.lookAt(0,0,0); // Keep looking at the center


      renderer.render(scene, camera);
    };

    if (animationFrameIdRef.current) {
        cancelAnimationFrame(animationFrameIdRef.current);
    }
    animationFrameIdRef.current = requestAnimationFrame(animate);


    const handleResize = () => {
      const camera = cameraRef.current;
      const renderer = rendererRef.current;
      if (!currentMount || !camera || !renderer) return;
      const newWidth = currentMount.clientWidth;
      const newHeight = currentMount.clientHeight;

      if (newWidth > 0 && newHeight > 0) {
        camera.aspect = newWidth / newHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(newWidth, newHeight);
      }
    };
    window.addEventListener('resize', handleResize);
    handleResize(); // Initial call

    return () => {
      if (animationFrameIdRef.current) {
        cancelAnimationFrame(animationFrameIdRef.current);
        animationFrameIdRef.current = null;
      }
      window.removeEventListener('resize', handleResize);

      const scene = sceneRef.current;
      if (scene) {
        projectMeshesRef.current.forEach(mesh => {
            if (mesh.geometry) mesh.geometry.dispose();
            if (mesh.material && typeof (mesh.material as THREE.Material).dispose === 'function') {
              (mesh.material as THREE.Material).dispose();
            }
            scene.remove(mesh);
        });
        meteorsRef.current.forEach(meteor => {
            if (meteor.mesh.geometry) meteor.mesh.geometry.dispose();
            if (meteor.mesh.material && typeof (meteor.mesh.material as THREE.Material).dispose === 'function') {
              (meteor.mesh.material as THREE.Material).dispose();
            }
            scene.remove(meteor.mesh);
        });
        if (backgroundGeometryRef.current) backgroundGeometryRef.current.dispose();
        if (backgroundShaderMaterialRef.current) backgroundShaderMaterialRef.current.dispose();
      }
      projectMeshesRef.current = [];
      meteorsRef.current = [];


      if (rendererRef.current) {
        rendererRef.current.dispose();
        if (rendererRef.current.domElement?.parentNode === currentMount) {
           currentMount.removeChild(rendererRef.current.domElement);
        }
      }
      sceneRef.current = null;
      cameraRef.current = null;
      rendererRef.current = null;
      backgroundShaderMaterialRef.current = null;
      backgroundGeometryRef.current = null;
    };
  }, [theme, projects, themeColors]);

  return (
    <div id="three-background-canvas" ref={mountRef} className="w-full h-full" />
  );
};

export default ThreeCanvas;

